  height: 300px;
  width: 320px;
  float: contour;
}

.video-card-reco {
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  width: 290px;
  height: 170px;
  margin: 0 30px 30px 30px;
  box-sizing: border-box;
  /*text-align:center;*/
  vertical-align: middle;
  display: table-cell;
}

.video-card-reco:before {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 48px;
  background-size: contain;
  background-repeat: repeat-x;
  border-radius: 0 0 2px 2px;
}

.van-watchlater span {
