}

.video-card-reco .info-box .info .title {
  font-size: 14px;
  line-height: 18px;
  height: 18px;
  color: #fff;
  margin-bottom: 6px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 500;
}

.video-card-reco .info-box .info, .video-card-reco .info-box .info {
  font-size: 12px;
  color: #ffffff;
  margin-bottom: 3px;
  line-height: 15px;
}

.video-card-reco:hover .info {
  top: -4%;
  opacity: 1;
  position: absolute;
}

.info {
  width: 50px;
  padding: 10px;
  /*opacity: 0;*/
  transition: opacity .2s;
}
.parttop{
  padding: 10vw 0 3vw 0;
}
.bgbottom {
  background-image: linear-gradient(#000000, #27214b);
}


</style>



